<template>
<div>
    <el-row :gutter="20" class="title">
        <el-col :span="6">
            <div class="text infoType">{{ methodObj[method] }}</div>
        </el-col>
        <el-col :span="10" :offset="8">
            <div class="moneyNumber">+{{ compoundPrice.fee }}</div>
        </el-col>
    </el-row>
    <el-row :gutter="20" class="content">
        <el-col :span="6">
            <div class="text orderDate">{{ compoundPrice.date }}</div>
        </el-col>
        <el-col :span="10" :offset="8">
            <div class=" text balance">
                <span class="balanceTitle">{{ $t('bill.balance') }}</span>
                <span class="balanceNumber">0000</span>
            </div>
        </el-col>
    </el-row>
    <el-divider></el-divider>
</div>
</template>

<script>
export default {
    props: ['information', 'method'],
    data() {
        return {
            methodObj: {
                income: this.$t('bill.onlineEarnings'),
                withdrawal: this.$t('bill.withdrawalTitle'),
                refund: this.$t('bill.refundTitle'),
            },
        };
    },
    computed: {
        compoundPrice() {
            if (this.method === 'withdrawal') {
                return {
                    fee: this.information.fee,
                    date: this.$d(new Date(this.information.success_time), 'long'),
                };
            } else {
                return {
                    fee: this.information.order_total_price,
                    date: this.$d(new Date(this.information.order_date), 'long'),
                };
            }
        },
    },
};
</script>

<style lang="scss" scoped>
.moneyNumber {
    font-size: 13px;
    font-weight: bold;
}

.infoType {
    font-weight: bold;
}

.text {
    font-size: 10px;
}

.title {
    padding: 1em 0;
}

.balanceTitle {
    padding-right: 3em;
}

.balanceNumber {
    font-weight: 600;
}
</style>
